<template>
  <v-container>
    <v-toolbar color="#121212" flat>
      <v-row align="center" justify="center">
        <v-btn v-for="(route, id) in routes" rounded :key="id" text>
          <router-link class="white-text" color="white" :to="route.route"
            ><div class="body-1 font-weight-light">
              {{ route.name }}
            </div></router-link
          >
        </v-btn>
      </v-row>
    </v-toolbar>
  </v-container>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      routes: [
        { name: "home", route: "/" },
        { name: "about", route: "/about" },
        { name: "projects", route: "/projects" },
      ],
      computed: {
        width() {
          switch (this.$veutify.breakpoint.name) {
            case "xs":
              return 150;
            case "sm":
              return 200;
            case "md":
              return 300;
            case "lg":
              return 400;
            case "xl":
              return 500;
          }
        },
      },
    };
  },
};
</script>

<style scoped>
a {
  text-decoration: none;
}
.white-text {
  color: white;
}
</style>
