<template>
  <v-container>
    <Header />
    <v-row class="mx-10 my-2">
      <v-col
        align-content="center"
        align="center"
        justify="center"
        cols="12"
        v-for="(project, index) in projects"
        :key="index"
      >
        <v-card elevation="5" rounded min-width="250px" max-width="60%">
          <v-container>
            <div class="text-h4 font-weight-light">{{ project.name }}</div>
          </v-container>
          <div class="text-h6 font-weight-light" v-if="project.end_month">
            {{ project.start_month }} - {{ project.end_month }}
          </div>
          <div class="text-h6 font-weight-light" v-else>
            {{ project.start_month }}
          </div>
          <v-container
            class="ma-2"
            align-content="center"
            align="center"
            justify="center"
          >
            <v-icon
              v-for="(icon, id) in project.icons"
              :key="id"
              x-large
              class="mx-1"
              :color="icon.colour"
            >
              {{ icon.icon }}
            </v-icon>
          </v-container>
          <v-img
            :max-width="project.image.width"
            :max-height="project.image.height"
            :src="srcs[projects.length - index - 1]"
            :lazy-src="project.image.lazy_src"
            :alt="project.image.alt"
          >
          </v-img>
          <v-container :v-if="project.image" class="mt-3">
            <a target="_blank" :href="project.demo_link">
              <v-icon class="mx-2" x-large> mdi-human-male-board </v-icon>
            </a>
            <a target="_blank" :href="project.git_link">
              <v-icon class="mx-2" x-large> fab fa-github </v-icon>
            </a>
          </v-container>
          <v-container>
            <div class="body-2 font-weight-light mx-16 mb-5">
              {{ project.description }}
            </div>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
    <v-container> </v-container>
    <Footer />
  </v-container>
</template>

<script>
import json from "../../projects.json";
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
export default {
  name: "ProjectPage",
  data() {
    return {
      projects: json.projects,
      srcs: {
        0: require("../assets/tutor-finder.png"),
        1: require("../assets/finance-tracker.png"),
        2: "",
        3: require("../assets/pokemon-team-builder.png"),
      },
      computed: {
        width() {
          switch (this.$veutify.breakpoint.name) {
            case "xs":
              return 150;
            case "sm":
              return 200;
            case "md":
              return 300;
            case "lg":
              return 400;
            case "xl":
              return 500;
          }
        },
        height() {
          switch (this.$veutify.breakpoint.name) {
            case "xs":
              return 150;
            case "sm":
              return 200;
            case "md":
              return 300;
            case "lg":
              return 400;
            case "xl":
              return 500;
          }
        },
      },
    };
  },
  components: {
    Header,
    Footer,
  },
};
</script>
<style scoped>
a {
  text-decoration: none;
}
</style>
